.ama__layout--three-column {
  display: grid;
  display: -ms-grid;
  grid-template-columns: 1fr 2fr  1fr;
  grid-template-rows: auto auto auto;
  min-height: 30em;
  -ms-grid-columns: 1fr 2fr 1fr;
  -ms-grid-rows: auto auto auto;
}

.ama__layout--three-column__page-content {
  grid-column: 1 / 5;
  grid-row: 1;

  @include breakpoint($bp-small min-width) {
    grid-column: 1 / 4;
    grid-row: 1 / 4;
  }

  @include breakpoint($bp-med min-width) {
    grid-column: 2;
    grid-row: 1 / 4;
    -ms-grid-column: 2;
    -ms-grid-row: 1;
  }
}// end article

.ama__layout--three-column__sidebar {
  &--primary {
    grid-column: 1 / 5;
    grid-row: 2;
    -ms-grid-row: 2;

    @include breakpoint($bp-small min-width) {
      grid-column: 4;
      grid-row: 1 / 3;
      -ms-grid-row: 1;
    }

    @include breakpoint($bp-med min-width) {
      grid-column: 1;
      grid-row: 1 / 4;
      -ms-grid-column: 1;
      -ms-grid-row: 1;
    }
  } // end aside.primary

  &--secondary {
    grid-column: 1 / 5;
    grid-row: 3 / 4;

    @include breakpoint($bp-small min-width) {
      grid-column: 4;
      grid-row: 3 / 4;
    }

    @include breakpoint($bp-med min-width) {
      grid-column: 3;
      grid-row: 1 / 4;
      -ms-grid-column: 3;
      -ms-grid-row: 1;
    }
  } // end aside.secondary
}
