%ama__button,
.ama__button,
button {
  @include ama-button();

  &--secondary {
    @include ama-button("", "secondary");
  }

  &--block {
    @include ama-button("block", "");
  }

  &--small {
    @include ama-button("small", "");
  }

  &--block,
  &--secondary {
    @include ama-button("block", "secondary");
  }

  &--small,
  &--secondary {
    @include ama-button("block", "secondary");
  }
}
